/* styles.css */
.board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    gap: 6px;
    margin: auto;
  }

  .tile {
    width: 100px;
    height: 100px;
    background-color: white;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    font-weight: bold;
    flex-direction: column; /* Removed quotes */
    align-items: center; /* Removed quotes */
    transition: transform 0.3s ease;    
  }

  .selected {
    z-index: 1; /* Added z-index to make it render over other tiles */
  }

  .tile p {
    margin: -5px 0; /* Adjust as needed */
  }
    
  .circle-button {
    position: absolute;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #ccc; /* Change this to your desired background color */
    border: none;
    outline: none;
    cursor: pointer;
    font-weight: bold;
    z-index: 2;
  }

  .circle-button:disabled {
    background-color: #ddd; /* Change this to your desired background color for disabled state */
    cursor: default;
    opacity: 0.5; /* You can adjust the opacity to make it visually appear disabled */
}
